import { useQuery, gql, useMutation } from '@apollo/client';
import { makeStyles, Theme } from '@material-ui/core/styles';
import {
	IconButton,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	useTheme,
	Alert,
	Checkbox,
} from '@material-ui/core';
import { visuallyHidden } from '@material-ui/utils';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { Link, generatePath, useHistory } from 'react-router-dom';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import React from 'react';
import { TForm } from '../../../types';
import config from '../../../config';
import Loader from '../../../ui-component/Loader';
import graphql from '../../../graphql';
import Header from './ListHeader';
import { getComparator, stableSort } from '../../../utils/table';
import LoadMore from '../components/LoadMore';
import { deleteForm } from '../../../graphql/mutations';
import { formatDate } from '../../../helpers/date';
import { useOrder, useSelectRow } from '../../../hooks';
import FormToolbar from './FormToolbar';
import { mapFormsCategory, NewTForm } from './helpers';
import { PATHS } from '../../../routes/path';

// style constant
const useStyles = makeStyles((theme: Theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	sortSpan: { ...visuallyHidden },
}));

const FormList = () => {
	const { loading, error, data, fetchMore } = useQuery(gql(graphql.listForms), {
		variables: { limit: config.pagination.limit },
		fetchPolicy: 'cache-first',
		notifyOnNetworkStatusChange: true,
	});

	const { data: listFormCategories } = useQuery(gql(graphql.listFormCategories));

	const [removeForm, { loading: isDeleteLoading, error: isDeleteError }] =
		useMutation(gql(deleteForm), {});

	const rows: TForm[] = _get(data, ['listForms', 'items'], []);
	const nextToken: string | null | undefined = _get(
		data,
		'listForms.nextToken',
		undefined
	);
	const theme = useTheme();
	const classes = useStyles();
	const { order, orderBy, handleRequestSort } = useOrder('CreatedAt');
	const { selected, isSelected, handleSelect, handleSelectAll, clearSelected } =
		useSelectRow('FormId');

	const history = useHistory();

	if ((loading || isDeleteLoading) && !data) return <Loader />;
	if (error || _isEmpty(data) || isDeleteError) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Something went wrong!
			</Alert>
		);
	}

	const loadMore = () => {
		fetchMore({
			variables: {
				limit: config.pagination.limit,
				nextToken,
			},
		});
	};

	const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
		handleSelectAll(event.target.checked, rows);
	};

	const onDeleteHandler = async () => {
		if (!selected) return;
		// eslint-disable-next-line no-restricted-syntax
		for await (const formId of selected) {
			await removeForm({
				variables: { FormId: formId },
				update: cache => {
					cache.modify({
						fields: {
							listForms(forms, { readField }) {
								const items = forms.items.filter(
									(item: TForm) => formId !== readField('FormId', item)
								);
								return { ...forms, items };
							},
						},
					});
				},
			});
		}
		clearSelected();
	};

	const onCreateHandler = () => {
		const formAddLink = generatePath(PATHS.FORM_ADD);
		history.push(formAddLink);
	};

	const mappedCategoryRows: Array<NewTForm | TForm> = mapFormsCategory(
		rows,
		_get(listFormCategories, ['listFormCategories'], [])
	);
	const sortedRows = stableSort<NewTForm>(
		mappedCategoryRows,
		getComparator(order, orderBy)
	);

	return (
		<>
			<FormToolbar
				numSelected={selected.length}
				onDelete={onDeleteHandler}
				onCreate={onCreateHandler}
			/>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header
						numSelected={selected.length}
						order={order}
						orderBy={orderBy}
						onSelectAllClick={handleSelectAllClick}
						onRequestSort={handleRequestSort}
						rowCount={rows.length}
					/>
					<TableBody>
						{sortedRows.map((row, index) => {
							if (typeof row === 'number') return null;
							const formEditLink = generatePath(PATHS.FORM, {
								id: row.FormId,
							});
							const isItemSelected = isSelected(row.FormId);
							const labelId = `enhanced-table-checkbox-${index}`;
							return (
								<TableRow
									hover
									onClick={() => handleSelect(row.FormId)}
									role='checkbox'
									aria-checked={isItemSelected}
									tabIndex={-1}
									key={row.FormId}
									selected={isItemSelected}
								>
									<TableCell sx={{ pl: 3 }} padding='checkbox'>
										<Checkbox
											color='primary'
											checked={isItemSelected}
											inputProps={{
												'aria-labelledby': labelId,
											}}
										/>
									</TableCell>
									<TableCell align='left'>{row.Title}</TableCell>
									<TableCell align='left'>
										{row.CategoryName || row.CategoryId}{' '}
									</TableCell>
									<TableCell align='left'>
										{row.FormTypeName || row.FormTypeId}
									</TableCell>
									<TableCell align='left'>{formatDate(row.CreatedAt)}</TableCell>
									<TableCell align='left'>{formatDate(row.UpdatedAt)}</TableCell>
									<TableCell align='left' sx={{ pr: 3 }}>
										<Link to={formEditLink}>
											<IconButton color='primary'>
												<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
											</IconButton>
										</Link>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			<LoadMore onLoadMore={loadMore} disabled={!nextToken} loading={loading} />
		</>
	);
};
export default FormList;
